//CSS REVISADO

//diseño general para los servicios
.servicios{
    width: 80%;
    margin: auto;
    a{
        .imagen-texto{
            img{
                height: 60% !important;
                object-fit: cover;
            }
            p{
                font-size: 2rem;
                font-style: italic;
            }
        }
        transition: all 0.6s ease;
        &:hover{
            transform: scale(1.05);
        }
    }
    @include tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 4rem;
        width: 90%;
    }
}

// diseño individual del servicio y del blog
.imagen-servicio{
    width: 100%;
    margin: auto;
    img{
        border-radius: 1rem;
    }
}
.info-servicio{
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    .texto-servicio{
        margin: 2rem 4rem;
    }
    .boton{
        display: inline-block;
        margin: 0 auto;
        padding: 0.5rem 3rem;
    }
}