.filtro{
    max-width: 100%;
    border: 4px solid $blanco;
    border-radius: 2rem;
    padding: 2rem;
    margin: 4rem auto;
    display: flex;
    flex-direction: column;
    select,input{
    //     text-overflow: ellipsis;
        width: 50%;
        margin: 0.1rem auto;
    //     padding: 0.5rem 0;
        font-size: 1.4rem;
        text-align: center;
        color: $blanco;
        background-color: $azul;
        border-radius: 6px;
        border: 2px solid $azul-intenso;
        // transition: all 0.6s ease;
        &:hover{
            color: $negro;
            background-color: $blanco;
            cursor: pointer;
        }
    }
    input[type="submit"]{
        display: inline !important;
        margin: auto !important;
    }
    @include desktop{
        flex-direction: row;
        justify-content: space-between;
        max-width: 90%;
    //     align-items: center;
        select,input{
            width: auto;
        }
    }
}